<template>
    <section class="contact section" id="contact">
        <h2 class="section__title">{{ $t('contact_me.title') }}</h2>
        <span class="section__subtitle">{{ $t('contact_me.subtitle') }}</span>

        <div class="contact__container container grid">
            <div>
                <div class="contact__information">
                    <i class="uil uil-phone contact__icon"></i>

                    <div>
                        <h3 class="contact__title">{{ $t('contact_me.call_me') }} 
                            <!-- ( <i class="uil uil-whatsapp"></i> <i class="uil uil-telegram"></i> ) -->
                        </h3>
                        <span class="contact__subtitle">+6281575823200</span>
                    </div>
                </div>
                <div class="contact__information">
                    <i class="uil uil-envelope contact__icon"></i>

                    <div>
                        <h3 class="contact__title">{{ $t('contact_me.email') }}</h3>
                        <span class="contact__subtitle">adibtambak@gmail.com</span>
                    </div>
                </div>
                <div class="contact__information">
                    <i class="uil uil-map-marker contact__icon"></i>

                    <div>
                        <h3 class="contact__title">{{ $t('contact_me.location') }}</h3>
                        <span class="contact__subtitle">purwodadi, indonesia</span>
                    </div>
                </div>
            </div>

            <form action="" class="contact__form grid">
                <div class="contact__content">
                    <label for="" class="contact__label">{{ $t('contact_me.form.name') }}</label>
                    <input type="text" class="contact__input" v-model="name">
                </div>
                <div class="contact__content">
                    <label for="" class="contact__label">{{ $t('contact_me.email') }}</label>
                    <input type="email" class="contact__input" v-model="email">
                </div>

                <div class="contact__content">
                    <label for="" class="contact__label">{{ $t('contact_me.form.project') }}</label>
                    <input type="text" class="contact__input" v-model="project">
                </div>
                <div class="contact__content">
                    <label for="" class="contact__label">{{ $t('contact_me.form.message') }}</label>
                    <textarea name="" id="" cols="0" rows="7" class="contact__input" v-model="message"></textarea>
                </div>

                <div>
                    <a class="button button--flex" @click="sendEmail()" v-html="labelSendMessage">
                    </a>
                </div>
            </form>
        </div>
    </section>
</template>

<script>
    import emailjs, { init } from 'emailjs-com';
    import { ref, defineComponent } from "vue";
    init("user_nlgt1UUfr4EkqnHsA8LEq");

    export default defineComponent({
        data: function()
        {
            return {
                label_send_message: '',
                label_success: '',
                label_error: '',
                // form: {
                //     name: '',
                //     email: '',
                //     project: '',
                //     message: '',
                // },
            }
        },
        setup: function()
        {
            let name = ref(''), email = ref(''), project = ref(''), message = ref('');
            let labelSendMessage = ref('');
            const sendEmail = () => {
                labelSendMessage.value = '<div class="fa-3x" style="font-size: 1.2rem;"><i class="fas fa-spinner fa-spin button__icon" style="margin: auto;"></i></div>';

                emailjs.send('service_28kg51v', 'template_bezor77', {
                    name: name.value,
                    message: message.value,
                    project: project.value,
                    email: email.value,
                })
                .then(function (response) {
                    labelSendMessage.value = 'Success <i class="fas fa-check button__icon"></i>';
                    setTimeout(() => {
                        labelSendMessage.value = this.label_send_message+'<i class="uil uil-message button__icon"></i>';
                    }, 3000);
                    console.log('SUCCESS!', response.status, response.text);
                }, function (error) {
                    labelSendMessage.value = 'Failed <i class="fas fa-times"></i>';
                    setTimeout(() => {
                        labelSendMessage.value = this.label_send_message+'Send Message<i class="uil uil-message button__icon"></i>';
                    }, 3000);
                    console.log('FAILED...', error);
                });
            }
            
            return {
                labelSendMessage,
                sendEmail,
                name,
                email,
                project,
                message,
            }
        },
        methods: {
        },
        mounted: function()
        {
            let lang = this.$store.state.langUser;
            let i18n = this.$i18n.messages[lang];
            this.label_send_message = i18n.contact_me.label_send_message;
            this.label_error = i18n.contact_me.label_error;
            this.label_success = i18n.contact_me.label_success;
            this.labelSendMessage = this.label_send_message + '<i class="uil uil-message button__icon"></i>';
        },
        watch: {
            'name': function()
            {
                console.log(this.name);
            }
        }
    });
</script>