<template>
      <header class="header" id="header">
          <nav class="nav container">
              <a href="#" class="nav__logo">Adib</a>

              <div class="nav__menu" id="nav-menu">
                  <ul class="nav__list grid">
                      <li class="nav__item">
                          <a href="#home" class="nav__link">
                              <i class="uil uil-estate nav__icon"></i> {{ $t("nav.home") }}
                          </a>
                      </li>
                      <li class="nav__item">
                          <a href="#about" class="nav__link">
                              <i class="uil uil-user nav__icon"></i> {{ $t("nav.about") }}
                          </a>
                      </li>
                      <li class="nav__item">
                          <a href="#skills" class="nav__link">
                              <i class="uil uil-file-alt nav__icon"></i> {{ $t("nav.skills") }}
                          </a>
                      </li>
                      <li class="nav__item">
                          <a href="#qualification" class="nav__link">
                              <i class="uil uil-game-structure nav__icon"></i> {{ $t("nav.qualification") }}
                          </a>
                      </li>
                      <li class="nav__item">
                          <a href="#portfolio" class="nav__link">
                              <i class="uil uil-scenery nav__icon"></i> {{ $t("nav.portfolio") }}
                          </a>
                      </li>
                      <li class="nav__item">
                          <a href="#contact" class="nav__link">
                              <i class="uil uil-message nav__icon"></i> {{ $t("nav.contact_me") }}
                          </a>
                      </li>
                  </ul>
                  <i class="uil uil-times nav__close" id="nav-close"></i>
              </div>

              <div class="nav__btns">
                    <div class="change__language__wrap">
                        <i class="fas fa-language" id="change-language"></i>
                        <div id="change-language-content">
                            <a @click="changeLanguage('id')">ID</a>
                            <a @click="changeLanguage('en')">EN</a>
                        </div>
                    </div>
                  <i class="uil uil-moon change-theme" id="theme-button"></i>
                  <div class="nav__toggle" id="nav-toggle">
                      <i class="uil uil-apps"></i>
                  </div>
              </div>
          </nav>
      </header>
</template>

<script>
import { defineComponent } from "@vue/runtime-core";

    export default defineComponent({
        mounted: function()
        {
            /*==================== MENU SHOW Y HIDDEN ====================*/
            const navMenu = document.getElementById('nav-menu'),
                navToggle = document.getElementById('nav-toggle'),
                navClose = document.getElementById('nav-close')

            /*===== MENU SHOW =====*/
            /* Validate if constant exists */
            if (navToggle) {
                navToggle.addEventListener('click', () => {
                    navMenu.classList.add('show-menu');
                    // navMenu.classList.add('')
                    console.log('show menu');
                })
            }

            /*===== MENU HIDDEN =====*/
            /* Validate if constant exists */
            if (navClose) {
                navClose.addEventListener('click', () => {
                    navMenu.classList.remove('show-menu');
                    console.log('hide menu');
                })
            }

            /*==================== REMOVE MENU MOBILE ====================*/
            const navLink = document.querySelectorAll('.nav__link')

            function linkAction() {
                const navMenu = document.getElementById('nav-menu')
                // When we click on each nav__link, we remove the show-menu class
                navMenu.classList.remove('show-menu');
            }
            navLink.forEach(n => n.addEventListener('click', linkAction))


            /*==================== DARK LIGHT THEME ====================*/ 

            const themeButton = document.getElementById('theme-button');
            const darkTheme = 'dark-theme';
            const iconTheme = 'uil-sun';

            const selectedTheme = localStorage.getItem('selected-theme');
            const selectedIcon = localStorage.getItem('selected-icon');

            const getCurrentTheme = () => document.body.classList.contains(darkTheme) ? 'dark' : 'light';
            const getCurrentIcon = () => themeButton.classList.contains(iconTheme) ? 'uil-moon' : 'uil-sun';

            if ( selectedTheme )
            {
                document.body.classList[selectedTheme === 'dark' ? 'add' : 'remove'](darkTheme);
                themeButton.classList[selectedIcon == 'uil-moon' ? 'add' : 'remove'](iconTheme);
            }

            themeButton.addEventListener('click', () => {
                document.body.classList.toggle(darkTheme);
                themeButton.classList.toggle(iconTheme);

                localStorage.setItem('selected-theme', getCurrentTheme());
                localStorage.setItem('selected-icon', getCurrentIcon());
            });

            // let changeLanguage = document.getElementById('change-language');
            // let languageContent = document.getElementById('change-language-content');
            //  changeLanguage.addEventListener('mouseover', () => {
            //      document.getElementById('change-language-content').style.display = 'block';
            // });
            //  changeLanguage.addEventListener('mouseout', () => {
            //      document.getElementById('change-language-content').style.display = 'none';
            // });
            //  languageContent.addEventListener('mouseover', () => {
            //      document.getElementById('change-language-content').style.display = 'block';
            // });
            //  languageContent.addEventListener('mouseout', () => {
            //      document.getElementById('change-language-content').style.display = 'none';
            // });
        },
        methods: {
            changeLanguage(lang)
            {
                this.$i18n.locale = lang;
                this.$store.state.langUser = lang;
                localStorage.setItem('lang', lang);
                // document.getElementById('change-language-content').style.display = 'none';
            }
        }

        // Change Language

    })
</script>