<script>
    export default {
        data: function()
        {
            return {
                qualification: '',
                localeQualification: {
                    en: {
                         education: [
                            {
                            status: 'Student',
                            place: 'Qodr HQ, DI Yogyakarta, Indonesian',
                            betweenYear: '2019 - 2022'
                            }
                        ],
                        work: [
                            {
                            status: 'Developers',
                            place: 'QodrBee, DI Yogyakarta, Indonesian',
                            betweenYear: '2019 - 2022'
                            },
                        ]
                    },
                    id: {
                         education: [
                            {
                            status: 'Siswa',
                            place: 'Qodr HQ, DI Yogyakarta, Indonesia',
                            betweenYear: '2019 - 2022'
                            }
                        ],
                        work: [
                            {
                            status: 'Developer',
                            place: 'QodrBee, DI Yogyakarta, Indonesia',
                            betweenYear: '2019 - 2022'
                            },
                        ]
                    }
                }
            }
        },
        mounted: function()
        {
            /*==================== QUALIFICATION TABS ====================*/
            const tabs = document.querySelectorAll('[data-target'),
                tabContents = document.querySelectorAll('[data-content');

            tabs.forEach(tab => {
                tab.addEventListener('click', () => {
                    const target = document.querySelector(tab.dataset.target);

                    tabContents.forEach(tabContent => {
                        tabContent.classList.remove('qualification__active');
                    })
                    target.classList.add('qualification__active');

                    tabs.forEach(tab => {
                        tab.classList.remove('qualification__active');
                    })
                    tab.classList.add('qualification__active');
                });
            });

            this.changeQualification();
        },
        methods: {
            changeQualification()
            {
                let langUser = this.$store.state.langUser;
                if ( langUser == 'en' )
                {
                    this.qualification = this.localeQualification.en;
                } else if ( langUser == 'id' )
                {
                    this.qualification = this.localeQualification.id;
                }
            }
        },
        watch: {
            '$store.state.langUser': function()
            {
                this.changeQualification();
            }
        }
    }
</script>