<template>
    <section class="portfolio section bd-container" id="portfolio">
        <h2 class="section__title">{{ $t('portfolio.title') }}</h2>
        <span class="section__subtitle">{{ $t('portfolio.subtitle') }}</span>

        <div class="portfolio__nav">
            <span class="portfolio__item portfolio__active" id="All" @click="activePortfolio('All')">All</span>
            <span class="portfolio__item" id="web" @click="activePortfolio('web')">Web</span>
            <span class="portfolio__item" id="mobile" @click="activePortfolio('mobile')">Mobile</span>
            <!-- <span class="portfolio__item" data-filter=".sertification" @click="activePortfolio(3)">Sertification</span>
            <span class="portfolio__item" data-filter=".sertification" @click="activePortfolio(3)">Desktop</span> -->
        </div>

        <div class="portfolio__container bd-grid">
            <!-- <div v-html="htmlPortfolio" style="display: contents;"></div> -->
            <div class="portfolio__content" v-for="(item, i) in filteredProject" :key="i">
                <a href="#"><img src="/img/portfolio1.jpg" alt="" class="portfolio__img"></a>
                <div class="portfolio__data">
                    <span class="portfolio__subtitle">{{ item.platform }}</span>
                    <a href="#"><h2 class="portfolio__title">{{ item.name }}</h2></a>
                    <a href="#" class="button button--link">View  Details</a>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    // import mixitup from 'mixitup';

    export default {
        data: function(){
            return {
                portfolio: '',
                htmlPortfolio: '',
                selectedPlatform: 'All',
                localePortofolio: {
                    en: [
                        {
                            platform: 'web mobile',
                            name: 'Gifari A Web Donation'
                        },
                        {
                            platform: 'mobile',
                            name: 'Gifari A Web Donation'
                        }
                    ],
                    id: [
                        {
                            platform: 'web mobile',
                            name: 'Gifari Sebuah Web Donasi'
                        },
                        {
                            platform: 'mobile',
                            name: 'Gifari Sebuah Web Donasi'
                        }
                    ]
                }
            }
        },
        mounted: function()
        {
            // mixitup('.portfolio__container', {
            //     selectors: {
            //         target: '.portfolio__content'
            //     },
            //     animation: {
            //         duration: 600
            //     }
            // });

            this.changePortfolio();
        },
        methods: {
            activePortfolio(platfrom)
            {
                this.selectedPlatform = platfrom;
                const portfolioItem = document.querySelectorAll('.portfolio__item');
                portfolioItem.forEach(pi => {
                    pi.classList.remove('portfolio__active');
                    if ( pi.id == platfrom )
                    {
                        pi.classList.add('portfolio__active');
                    }
                });
            },
            changePortfolio()
            {
                let langUser = this.$store.state.langUser;
                if ( langUser == 'en' )
                {
                    this.portfolio = this.localePortofolio.en;
                } else if ( langUser == 'id' )
                {
                    this.portfolio = this.localePortofolio.id;
                }
            }
        },
        watch: {
            "$store.state.langUser": function()
            {
                this.changePortfolio();
            }
        },
        computed: {
            filteredProject: function () {
                var vm = this;
                var platform = vm.selectedPlatform;

                if (platform === "All") {
                    return vm.portfolio;
                } else {
                    return vm.portfolio.filter(function (project) {
                        return project.platform.includes(platform);
                    });
                }
            }
        }
    }
</script>