<script>
    export default {
        data: function()
        {
            return {
                skill: '',
                skills: {
                    id: [
                            {
                                name: "Frontend Developers",
                                description: "Lebih Dari 4 tahun",
                                child: [ "HTML", "CSS", "Javascript", "Vue" ]
                            },
                            {
                                name: "Backend Developers",
                                description: "Lebih Dari 3 Tahun",
                                child: [ "PHP", "Laravel" ]
                            }
                        ],
                    en: [
                            {
                                name: "Frontend Developers",
                                description: "More than 4 years",
                                child: [ "HTML", "CSS", "Javascript", "Vue" ]
                            },
                            {
                                name: "Backend Developers",
                                description: "More than 3 Years",
                                child: [ "PHP", "Laravel" ]
                            }
                        ],
                }
            }
        },
        mounted: function()
        {
             this.changeSkill();
        },
        methods: {
            skillsToggle(id)
            {
                let skillsContent = document.getElementsByClassName('skills__content');
                
                for (let index = 0; index < skillsContent.length; index++) {
                    console.log(skillsContent[index].className);

                    let idSkillContent = skillsContent[index].id;
                    
                    if ( idSkillContent == id && !skillsContent[index].classList.contains('skills__open') ) // jika idSkillContent sama dengan params id dan skill content loopingan sekarang tidak punya class skills__open 
                    {
                        skillsContent[index].classList.add('skills__open');
                    } else // jika idSkillContent tidak sama dengan params id dan punya class skills__open maka hapus class skills_open dan skills__close, skills_close dihapus juga buat jaga2 ada 2 class yang sama
                    {
                        skillsContent[index].classList.remove('skills__open');
                        skillsContent[index].classList.remove('skills__close');
                        skillsContent[index].classList.add('skills__close');
                    }
                }
            },
            changeSkill()
            {
                let langUser = this.$store.state.langUser;
                if ( langUser == 'en' )
                {
                    console.log('dari langUser en');
                    // console.log(this.skills.en);
                    this.skill = this.skills.en;
                } else if ( langUser == 'id' )
                {
                    console.log('dari langUser id');
                    this.skill = this.skills.id;
                }
            }
        },
        watch: {
            '$store.state.langUser': function()
            {
              this.changeSkill();
            }
        }
    }
</script>