<template>
    <div class="testimonial section">
        <h2 class="section__title">{{ $t('testimonial.title') }}</h2>
        <span class="section__subtitle">{{ $t('testimonial.subtitle') }}</span>

        <div class="testimonial__container container">
             <swiper :slides-per-view="slidePerView" :cover-flow-effect="{slideShadow: true}" :space-between="50" @swiper="onSwiper" @slideChange="onSlideChange" :pagination="{ clickable: true }" :autoplay="{delay: 5000}">
                 <swiper-slide v-for="( item, i ) in testimonial" :key="i" style="box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px; padding: 1em">
                     <div class="testimonial__content">
                         <div class="testimonial__data">
                             <div class="testimonial__header">
                                 <img :src="'/img/'+item.foto" alt="" class="testimonial__img">

                                 <div class="testimonial__detail_client_wrap">
                                     <h3 class="testimonial__name">{{ item.name }}</h3>
                                     <span class="testimonial__client">{{ item.status }}</span>
                                 </div>
                             </div>

                             <div style="margin: 0 5%;">
                                 <template v-for="i in 5" :key="i">
                                    <i v-if="i < item.rating" style="color: yellow;" class="fas fa-star testimonial__icon-star"></i>
                                    <i v-else class="far fa-star testimonial__icon-star"></i>
                                 </template>
                             </div>
                         </div>

                         <p class="testimonial__description">{{ item.testimonial }}</p>
                     </div>
                 </swiper-slide>
             </swiper>
        </div>
    </div>
</template>

<script>
    import {
        Swiper,
        SwiperSlide
    } from 'swiper/vue';

    import SwiperCore, { Pagination, A11y, Autoplay } from 'swiper';
    
    SwiperCore.use([Pagination, A11y, Autoplay]);
    
    // Import Swiper styles
    import 'swiper/swiper.scss';
    import 'swiper/components/navigation/navigation.scss';
    import 'swiper/components/pagination/pagination.scss';
    import 'swiper/components/scrollbar/scrollbar.scss';
    
    export default {
        data: function()
        {
            return {
                slidePerView: 1,
                testimonial: '',
                localeTestimonial: {
                    en: [
                        {
                            name: 'Bambang1',
                            foto: 'testimonial1.jpg',
                            status: 'Project Manager',
                            rating: 5,
                            testimonial: 'english lorem ipsum dolor si memet temen ngeselinnya entong'
                        }
                    ],
                    id: [
                        {
                            name: 'Bambang1',
                            foto: 'testimonial1.jpg',
                            status: 'Project Manager',
                            rating: 5,
                            testimonial: 'udah di bahasa indonesiakan lorem ipsum dolor si memet temen ngeselinnya entong'
                        }
                    ],
                }
            }
        },
        components: {
            Swiper,
            SwiperSlide,
        },
        methods: {
            onSwiper(swiper) {
                console.log(swiper);
            },
            onSlideChange() {
                console.log('slide change');
            },
            changeTestimonial()
            {
                let langUser = this.$store.state.langUser;
                if ( langUser == 'en' )
                {
                    this.testimonial = this.localeTestimonial.en;
                } else if ( langUser == 'id' )
                {
                    this.testimonial = this.localeTestimonial.id;
                }
            }
        },
        mounted: function()
        {
            let widhtScreen = window.screen.width;
            if ( widhtScreen > 1024 )
            {
                this.slidePerView = 3;
            } else if ( widhtScreen > 770 )
            {
                this.slidePerView = 2;
            }

            this.changeTestimonial();
            console.log(widhtScreen);
        },
        watch: {
            "$store.state.langUser": function()
            {
                this.changeTestimonial();
            }
        }
    };
</script>